<template>
  <v-container
    id="listarClientes"
    fluid
    tag="section"
  >
    <v-btn
      to="/ventas/clientes/nuevo"
      fab
      dark
      color="purple"
      class="mx-5"
    >
      <v-icon>mdi-plus</v-icon>
    </v-btn>

    <v-btn
      to="/ventas"
      fab
      dark
      color="black"
      class="mx-5"
    >
      <v-icon>mdi-arrow-left</v-icon>
    </v-btn>

    <base-material-card
      icon="mdi-clipboard-text"
      title="Lista de Cientes"
      class="px-5 py-3"
    >
      <v-data-table
        :headers="cabecera"
        :items="clientes"
        sort-by="nombres"
        class="elevation-1"
      >
        <template v-slot:item.actions="{ item }">
          <v-btn
            :to="{ name: 'Editar Cliente', params: { id: item._id } }"
            fab
            small
            color="light-blue"
          >
            <v-icon> mdi-pencil </v-icon>
          </v-btn>
          <v-btn
            fab
            small
            color="red"
            class="mx-5"
            @click.stop="dialog = true"
            @click="id = item._id"
          >
            <v-icon> mdi-delete </v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </base-material-card>

    <v-dialog
      v-model="dialog"
      width="350"
    >
      <v-card>
        <v-card-title class="headline grey lighten-2">
          Desea eliminar el registro?
        </v-card-title>

        <v-divider />

        <v-card-actions>
          <v-spacer />
          <v-btn @click="dialog = false">
            Cancelar
          </v-btn>
          <v-btn
            color="error"
            @click="confirmarBorrado(id)"
          >
            Aceptar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
  import axios from 'axios'
  const urlCliente = 'https://sistema-adv.herokuapp.com/api/customer/'

  export default {
    name: 'ListarClientes',
    data () {
      return {
        dialog: false,
        cabecera: [
          { text: 'Nombre', align: 'center', value: 'name' },
          { text: 'DNI', value: 'document', align: 'center' },
          { text: 'Celular', value: 'cellphone', align: 'center' },
          { text: 'Email', value: 'email', align: 'center' },
          { text: 'Direccion', value: 'address', align: 'center' },
          {
            text: 'Acciones',
            value: 'actions',
            align: 'center',
            sortable: false,
          },
        ],
        clientes: [
          {
            name: '',
            document: '',
            cellphone: '',
            email: '',
            address: '',
          },
        ],
      }
    },
    mounted () {
      this.obtenerClientes()
    },
    methods: {
      obtenerClientes () {
        axios
          .get(urlCliente)
          .then((response) => {
            // console.log(response.data)
            this.clientes = response.data
          })
          .catch((err) => {
            console.log(err)
          })
      },
      confirmarBorrado (id) {
        axios
          .delete(urlCliente + id)
          .then((res) => {
            // console.log(id);
            this.obtenerClientes()
            this.dialog = false
          })
          .catch((e) => {
            console.log(e)
          })
      },
    },
  }
</script>
